@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  padding:0;
  margin:0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
html {
  scroll-behavior: smooth;
}
a{
  text-decoration: none;
  color:black;
}
.navbar{
    position:relative;
}
.navbar__hamburger{
    position:fixed;
    z-index:12;
    margin-top:1em;
    height:50px;
    right:5%;
    cursor:pointer;
}
.navbar__hamburger div{
    background:#FF6464;
    margin:.25em 0;
    width:30px;
    height:5px;
    transition: all 1s;
}
.hamburger__topBar--active{
    -webkit-transform: rotate(50deg);
            transform: rotate(50deg);
    transition: all 1s;
}
.hamburger__botBar--active{
    -webkit-transform: rotate(-50deg) translateY(-5px) translateX(7px);
            transform: rotate(-50deg) translateY(-5px) translateX(7px);
    transition: all 1s;
}
nav{
    position:fixed;
    width:100%;
    z-index:10;
    background:#f2e1a5;
    height:100vh;
    left:-100%;
    transition:all 1s;
}
.navbar__nav--active{
    top:0;
    left:0;
    transition:all 1s;
}
nav ul{
    display:flex;
    flex-direction: column;
    justify-content: space-around;
    height:100%;
    text-align: center;
    margin:1em;
}
li{
    list-style-type: none;
    font-size:2rem;
}
.header{
    padding:1em .5em 2em;
    background-color: #ece4e4;
    min-width: 320px;
}
.header__infoContainer{
    text-align: center;
}
.header__imgageContainer{
    margin:2em auto 1em;
    height: 300px;
    width:100%;
    max-width: 320px;
    border-radius: 100%;
    background-color: #f2d261;
    background-image: url(https://github.com/KanBoorodom/cryptocurrency/blob/master/src/component/illust/mee.png?raw=true);
    background-size: 120%;
    background-position: center;
    border: 10px solid #cfc7c7;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);
}
.fa-line,.fa-github-square{
    font-size:1.75rem;
}
.header p{
    font-weight: 400;
    font-size:2.1rem;
}
.header :nth-child(3){
    font-size: 1.35rem;
    color:#5C5858;
}
.header__download{
    color:#66520c;
    position:relative;
    font-size:1.5rem;
    font-weight: 500;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    margin:1em auto;
    cursor:pointer;
}
.header__download::after{
    content:'';
    position:absolute;
    background: #f7ff00;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #db36a4, #f7ff00); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background-size: 250% 100%;
    width:70%;
    height:25%;
    left:15%;    
    bottom:-20%;
    transition: all ease 1s;
}
.header__download:hover::after{
    background-position: 100% 0;
    transition: all ease 1s;
}
@media only screen and (min-width:375px) {
    .header{
        padding-left:1em;
        padding-right:1em;
    }
    .header__container{
        background:#dbd5d5;
        box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);
        margin:auto;
        padding:.5em;
    }
}
@media only screen and (min-width:768px) {
    .header{
        flex-direction: row;
        text-align: left;
        padding:2em;
    }
    .header__infoContainer{
        text-align: unset;
    }
    .header__container{
        display:flex;
        flex-direction: row;
        text-align: left;
        background:#dbd5d5;
        box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);
        margin:auto;
        padding:1em;
    }
    .header__infoContainer{
        width:60%;
        padding:5em 1em 2em 2em;
    }
    .header__download{
        font-size: 1.25rem !important;
        margin:1em 0 0 0;
    }
    .header__download::after{
        left:0;    
        bottom:-20%;
    }
    .header__download:hover::after{
        width:100%;
    }
}
@media only screen and (min-width:1024px) {
    .header{
        padding:3em 2em 5em;
    }
    .header__imgageContainer{
        max-width: 350px;
        height: 350px;
        border: 18px solid #cfc7c7;
    }
    .header__infoContainer{
        width:auto;
    }
    .headerInfo__hello{
        font-size:3.25rem !important;
    }
    .headerInfo__myName,.headerInfo__myName span{
        font-size:3.5rem !important;
    }

}
@media only screen and (min-width:1440px) {
    .header__imgageContainer{
        max-width: 400px;
        height: 400px;
    }
    .header__infoContainer{
        margin:0;
        width:60%;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        align-self: center;
    }
}
.about{
    padding:.5em;
    background-color:#ffff;
}
.about__header{
    margin-top:.75em;
    font-size:1.75rem;
    font-weight: 400;
}
.about__info{
    margin-bottom:.5em;
}
.aboutInfo__p{
    font-size: 1.15rem;
    text-indent: 25px;
    color: #5C5858;
    margin-bottom:1em;
}
.aboutInfo__img{
    width:80%;
    max-width: 350px;
    max-height: 350px;
    display:block;
    margin:1em auto;
    border-radius: 15px;
}
.aboutEx__header{
    display: flex;
    justify-content: space-around;
    margin-top:.5em;
}
.aboutEx__header p{
    padding:.25em .5em;
    cursor:pointer;
}
.aboutEx__header--active{
    background-color:#ea6028;
    font-weight: 500;
    color:white;
    border-radius: 15px;
    transition: all 1s;
}

.aboutEx__info{
    margin-top:1em;
    padding:.25em 1em;
}
.about__skill{
    background-color:#f2d261;
    text-align: center;
    margin-top:2em;
    padding:.5em 1em;
    border-radius: 10px;
}
.about__skill h2{
    color:#645825;
}
.about__skill p{
    font-weight: 600;
    color:#473c13;
}
@media only screen and (min-width:450px) {
    .aboutInfo__p{
        padding:1em;
        text-align: center;
    }
}
@media only screen and (min-width:768px) {
    .about{
        padding:1.5em;
    }
    .about__experience{
        width:80%;
        margin:auto;
    }
}
@media only screen and (min-width:1000px) {
    .about{padding-top: 3em;}
    .about h2{
        font-size:2.5rem;
    }
    .about__info{
        display: flex;
        flex-direction: row-reverse;
    }
    .aboutInfo__p{
        width:90%;
        text-align: left;
    }
    .about__experience{
        width:95%;
    }
    .aboutInfo__img{
        max-width: 500px;
        max-height: 500px;
    }
    .about__skill{
        width:65%;
        margin:3em auto 0;
    }

}
.project{
    margin-top:2em;
    padding:1em;
    background-color:#ece4e4;
}
.project h2{
    text-align: center;
    position:relative;
}
.project h2::after{
    content:'My lastest project';
    position:absolute;
    bottom:-80%;
    left:0;
    font-size:.8rem;
    height:100%;
    width:100%;
    color:#ea6028;
}
.project__card{
    position:relative;
    z-index:1;
    margin:2em auto;
    padding:7em 1em 1em;
    background-size: 100%;
    height:350px;
    width:100%;
    max-width: 350px;
    text-align: center;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);
    transition: all 1s ease;
}
.project__card:hover{
    background-size: 115%;
    transition: all 1s ease;
}
.project__card::after{
    content:'';
    position:absolute;
    background-color:#3b3838;
    border-radius: 10px;
    width:100%;
    height:100%;
    top:0;
    left:0;
    opacity:.55;
    z-index:-1;
}
.project__card:hover::after{
    opacity:.4;
}
.project__card img{
    width:100%;
}
.projectCard__name{
    font-size: 1.5rem;
    font-weight: 500;
    color:white;
}
.projectCard__btn{
    font-size:1.15rem;
    font-weight: 300;
    border:2px solid white;
    color:white;
    display:inline-block;
    margin-top:1em;
    padding:.25em .75em;
    transition: all ease 1s;
}
.projectCard__btn:hover{
    background-color: #de0000;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    border:none;
    transition: all ease 1s;
}
@media only screen and (min-width:700px) {
    .project__cardontainer{
        display:flex;
        flex-wrap: wrap;
    }
}
@media only screen and (min-width:1000px) {
    .project{
        padding:3em 1em 3.5em;
    }
    .project h2{
        font-size:2.5rem;
        margin-bottom:2em;
    }
    .project h2::after{
        font-size:1.25rem;
    }
    .project__card{
        max-width: 450px;
        height:450px;
        padding-top:10em;
    }
}


.contact{
    position:relative;
    background-color: #f2d261;
    padding:1em 1em 4em;
}
.contact h2{
    color:#645825;
    text-align: center;
}
.contact__form{
    background-color:#dfc155; /* #e7a7a7 */
    padding:1em;
    display:flex;
    flex-direction: column;
    grid-gap:.75em;
    gap:.75em;
    border-radius: 10px;
}
.contact__form div{
    font-size:1.25rem;
    text-align: center;
    color:red;
    font-weight: 500;
}
.contact__form input,.contact__form textarea{
    padding:.25em;
    border:none;
    background-color: #eae4cf;
}
.contact__form input:focus,.contact__form textarea:focus{
    background-color:white;
    outline: none;
 }

.contactForm__submit{
    background: #f7ff00;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #db36a4, #f7ff00); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background-size: 150%;
    width:80%;
    align-self: center;
    border-radius: 12px;
    font-size:1.15rem;
    font-weight: 500;
    transition: all ease 1s;
    cursor:pointer;
}
.contactForm__submit:hover{
    background-position: 100% 0;
    transition: all ease 1s;
}
input:valid,textarea:valid {
    background-color: #ddffdd;
  }
  .sendEmail{
      position:fixed;
      z-index:999;
      border-radius: 15px;
      top:40%;
      font-weight: 600;
      text-align: center;
      background-color:white;
      box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);
      width:90%;
      margin:auto;
      padding:.5em;
}
.sendEmail button{
    font-weight: 500;
    margin-top:1em;
    padding:.125em .7em;
}
/* input:invalid,textarea:invalid {
    background-color: #f28b8b;
  } */

  @media only screen and (min-width:700px) {
    .contact{
        padding-top:3em;
    }
    .contact h2{
        font-size:1.75rem;
    }
    .contact__form{
        width:50%;
        margin:1em auto;
    }
    .contactForm__submit{
        width:50%;
    }
}
@media only screen and (min-width:1000px) {
    .contact h2{
        font-size:2.5rem;
    }
}
